<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="pos-order" id="order-list">
        <el-tabs activeName="first" type="border-card">
          <el-tab-pane label="点餐" name="first">
            <el-table :data="tableData" border style="width:100%;">
              <el-table-column prop="goodsName" label="商品名称">
              </el-table-column>
              <el-table-column prop="count" label="数量" width="60"></el-table-column>
              <el-table-column prop="price" label="金额" width="60"></el-table-column>
              <el-table-column  label="操作" fixed="right">
                  <template slot-scope="scope">
                      <el-button type="text" size="mini" @click="addCargo(scope.row)">+</el-button>
                      <el-button type="text" size="mini" @click="subCargo(scope.row)">-</el-button>
                      <el-button type="text" size="mini" @click="delSingleGoods(scope.row)">删除</el-button>
                  </template>
              </el-table-column>
            </el-table>
            <el-row>
                <el-col :span='12'>
                    <small>数量</small>
                    <b>{{totalCount}}</b>
                </el-col>
                <el-col :span='12'>
                    <small>金额</small>
                    <b>{{totalMoney}}元</b>
                </el-col>
            </el-row>
            <div class="div-btn">
              <el-button type="warning">挂单</el-button>
              <el-button type="danger" @click="delAllGoods">删除</el-button>
              <el-button type="success" @click="checkout">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单" >
            挂单
          </el-tab-pane>
          <el-tab-pane label="外卖" >
            外卖
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="17">
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for="item in oftenGoods" :key="item.id" @click="addCargo(item)">
                <span>{{item.goodsName}}</span>
                <span class="o-price">￥{{item.price}}元</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
          <el-tabs type="border-card">
            <el-tab-pane label="汉堡">
              <ul class='cookList'>
                <!-- http://iph.href.lu/190x200 -->
                <li v-for="item in type0Goods" :key="item.id" @click="addCargo(item)">
                    <!-- <span class="foodImg"><img :src="item.goodsImg" width="100%"></span> -->
                    <span class="foodImg"><img src="http://iph.href.lu/190x200" width="100%"></span>
                    <span class="foodName">{{item.goodsName}}</span>
                    <span class="foodPrice">￥{{item.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <ul class='cookList'>
                <li v-for="item in type1Goods" :key="item.id" @click="addCargo(item)">
                    <span class="foodImg"><img src="http://iph.href.lu/190x200" width="100%"></span>
                    <span class="foodName">{{item.goodsName}}</span>
                    <span class="foodPrice">￥{{item.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <ul class='cookList'>
                <li v-for="item in type2Goods" :key="item.id" @click="addCargo(item)">
                    <span class="foodImg"><img src="http://iph.href.lu/190x200" width="100%"></span>
                    <span class="foodName">{{item.goodsName}}</span>
                    <span class="foodPrice">￥{{item.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <ul class='cookList'>
                <li v-for="item in type3Goods" :key="item.id" @click="addCargo(item)">
                    <span class="foodImg"><img src="http://iph.href.lu/190x200" width="100%"></span>
                    <span class="foodName">{{item.goodsName}}</span>
                    <span class="foodPrice">￥{{item.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'pos',
  data(){
    return {
      tableData:[],
      oftenGoods:[],
      type0Goods:[{
              goodsId:1,
              goodsImg:"http://iph.href.lu/190x200",
              goodsName:'香辣鸡腿堡',
              price:18
      }],
      type1Goods:[],
      type2Goods:[],
      type3Goods:[],
      totalCount:0,
      totalMoney:0
    }
  },
  methods:{
    subCargo(cargo){
      if(cargo.count == 1){
        this.delSingleGoods(cargo);
      }else{
          let goodsArray = this.tableData.filter((item)=>item.goodsId == cargo.goodsId);
          goodsArray[0].count--;
          this.countAllMoney();
      }
    },
    addCargo:function(cargo){
      let flag = false;
      //商品是否存在
      this.tableData.forEach((item)=>{
        if(item.goodsId == cargo.goodsId){
          flag = true;
        }
      })
      if(flag){//存在
        let goodsArray = this.tableData.filter((item)=>item.goodsId == cargo.goodsId);
        if(goodsArray && goodsArray.length > 0){
          goodsArray[0].count++;
        }
      }else{//新增
        this.tableData.push({
          goodsId:cargo.goodsId,
          goodsName:cargo.goodsName,
          count:1,
          price:cargo.price
        });
      }
      this.countAllMoney();
      
    },
    delSingleGoods(goods){
      this.tableData = this.tableData.filter(o=>o.goodsId!=goods.goodsId)
      this.countAllMoney();
    },
    countAllMoney(){
      if(this.tableData){
        this.totalCount = 0;
        this.totalMoney = 0;
        //计算总价
        this.tableData.forEach(item=>{
          this.totalCount += item.count;
          this.totalMoney = this.totalMoney + item.price*item.count;
        });
      }
    },
    delAllGoods(){
      this.tableData = [];
      this.totalCount = 0;
      this.totalMoney = 0;
    },
    checkout(){
      if(this.totalCount > 0){
        this.$message.success("结账成功,总金额:"+this.totalMoney+"元！");
        this.tableData = [];
        this.totalCount = 0;
        this.totalMoney = 0;
      }else{
        this.$message.error("还没有选商品呢！");
      }
    }
  },
  created:function(){
    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/oftenGoods')
    .then(response => {
      
      if(response && response.status == 200){
        this.oftenGoods = response.data;
      }
      
    }).catch(error =>{
      console.log(error);
      alert('error')
    })
    axios.get('https://www.easy-mock.com/mock/5b8b30dbf032f03c5e71de7f/kuaican/typeGoods')
    .then(response => {
      console.log(response);
      if(response && response.status == 200){
        var dataList = response.data;
        this.type0Goods = dataList[0]
        this.type1Goods = dataList[1]
        this.type2Goods = dataList[2]
        this.type3Goods = dataList[3]
      }
      
    }).catch(error =>{
      console.log(error);
      alert('error')
    })
    
  },
  mounted:function(){
    var orderHeight = document.body.clientHeight;
    document.getElementById('order-list').style.height = orderHeight + 'px';
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .pos-order{
    background-color: #f9fafc;
    border-right: 1px solid #c0ccda;
    height: 100%;
  }
  .div-btn{
    margin-top: 10px;
  }
   .title{
       height: 20px;
       border-bottom:1px solid #D3DCE6;
       background-color: #F9FAFC;
       padding:10px;
   }
   .often-goods-list ul li{
      list-style: none;
      float:left;
      border:1px solid #E5E9F2;
      padding:10px;
      margin:5px;
      background-color:#fff;
   }
  .o-price{
      color:#58B7FF; 
   }
  .often-goods{
    display: inline-block;
    width: 100%;
  }
  .cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
   }
   .cookList li span{
        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;
   }
   .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }
</style>



